#about {
    main {
        padding: 10px;
        border: solid 2px white;
        background: linear-gradient(transparentize($cyan,0.5), transparentize($magenta,0.5)); }
    iframe {
        display: block;
        margin: 0 auto;
        width: 100%;
        height: 400px;
        margin-bottom: 15px; }

    p {
        margin-bottom: 10px;
        font-size: 1.1em;
        // background: transparentize($black, 0.5)
        padding: 10px;
        &::first-letter {
            text-transform: uppercase;
            font-size: 1.5em; }
        span {
            font-weight: bold;
            color: $deep-magenta;
            font-size: 1.2em;
            @supports (-webkit-background-clip: text) {
                background: linear-gradient(to right, $deep-cyan, $indigo, $deep-magenta);
                color: transparent;
                -webkit-background-clip: text; }
            @include mobile {
                color: $cyan;
                background: none; } } } }
