#song-list {
    .content-wrap {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        @include mobile {
            flex-flow: column wrap; } }
    .song {
        padding: 10px;
        background: white;
        display: flex;
        flex-direction: row;
        align-items: center;
        margin: 10px 0;
        max-width: calc( 50% - 10px);
        @include mobile {
            max-width: 100%;
            flex-direction: column;
            background: $black;
            border: solid $white 1px; }

        #song-picker-cover {
            max-height: 150px;
            margin-right: 20px;
            border: solid black 2px;
            @include mobile {
                margin-right: 0; } }

        #song-picker-title {
            font-size: 1.3em;
            font-family: $font3;
            color: $deep-magenta;
            -webkit-text-fill-color: transparent;
            background: linear-gradient(to right, $deep-magenta, $deep-cyan);
            -webkit-background-clip: text;
            margin-bottom: 10px;
            @include mobile {
                text-align: center; } }

        button {
            padding: 10px 20px;
            font-size: 1.2em;
            font-family: $font2;
            border: solid $deep-magenta 2px;
            border-radius: 5px;
            background: white;
            color: $deep-magenta;
            overflow: hidden;
            position: relative;
            transition: all 0.3s;
            &:hover {
                color: $magenta;
                border-color: $magenta; }
            @include mobile {
                display: block;
                margin: 0 auto; } }

        #download-link {
            @include mobile {
                display: none; } } } }
